import {Component, Input, OnInit} from '@angular/core';
import {Directors} from "../../../model/directors";
import {DirectorsService} from "../../../service/directors.service";
import {Facilities} from "../../../model/facilities";

@Component({
  selector: 'app-directors-center',
  templateUrl: './directors-center.component.html',
  styleUrls: ['./directors-center.component.css']
})
export class DirectorsCenterComponent implements OnInit {

  selectedDirectors: Directors;
  private hideNewDirectors: boolean = true;

  private isEmbedded: boolean;

  @Input("directors")
  directors: Array<Directors> = [];
  @Input("facility")
  public facility: Facilities;

  constructor(private directorsService: DirectorsService) {
    this.isEmbedded = JSON.parse(localStorage.getItem('isEmbedded'));

    /*if(this.isEmbedded == true) {
      this.directors = [];
    } else {
      this.directors = [{
        "facilitiesDirectorsId": "director id 1",
        "facilitiesDirectorsName": "director name",
        "facilitiesDirectorsEmail": "director name",
        "facilitiesDirectorsPhone": "director phone",
        "facilitiesDirectorsRoom": "director room",
        "facilitiesDirectorsInfo": "director info",
        "facilitiesDirectorsPosition": "director position",
        "facilitiesDirectorsPositionName": "director name",
        "facilitiesDirectorsEx1": "director extra information",
        "facilities": {
          "facilitiesId": "id 1",
          "facilitiesName": "name 1",
          "facilitiesInfo": "info 1"
        }
      }];
    }*/
  }

  ngOnInit() {
    console.log("Current facility in directors center: " + JSON.stringify(this.facility));

    if(this.isEmbedded == true) {
      //this.directorsService.getDirectors(this.facility).subscribe(resDirectors => this.directors = resDirectors);
    }
  }

  onSelectDirectors(directors: Directors) {
    this.selectedDirectors = directors;
  }

  onUpdateDirectorsEvent(directors: Directors) {
    if(this.isEmbedded == true) {
      this.directorsService.updateDirectors(this.facility, directors).subscribe(resDirectors => this.directors = resDirectors);
    }
  }

  onDeleteDirectorsEvent(directors: Directors) {
    if(this.isEmbedded) {
      this.directorsService.deleteDirectors(this.facility, directors).subscribe(() => {});
    }
  }

  onCreateDirectors() {
    if(this.isEmbedded == true) {
      this.hideNewDirectors = !this.hideNewDirectors;
    }
  }

  onSubmitNewDirectors(directors: Directors) {
    directors.facilitiesDirectorsId = this.facility.facilitiesName + "DirectorsId" + Math.floor((Math.random() * 100) + 1).toString() + "and" + Math.floor((Math.random() * 1000) + 3000).toString()
    if(this.isEmbedded == true) {
      this.directorsService.createDirectors(this.facility, directors).subscribe(resDirectors => this.directors = resDirectors);
    }
  }

}



